<template>
  <v-card>
    <v-card-text>
      <v-row>
        <v-col cols="12">
          <mrc_ess_autoCompleteOrg v-model="params.org_level_uuid"
                                   :disabled='disabled'
                                   :placeholder="$t('label_Use_the_unit')"
                                   :errMsg="$errors('params','org_level_uuid',requiredRule)"
                                   :label="$t('label_Use_the_unit') + '*'"></mrc_ess_autoCompleteOrg>
        </v-col>
        <v-col class="d-flex"
               cols="12"
               sm="6">
          <mrc_ess_datePicker :errMsg="$errors('params','effective_start_date',requiredRule.concat([{rule: 'beforeData',msg: '不能超过有效结束时间'}]))"
                              @blur="$v.params.effective_start_date.$touch()"
                              :disabled="disabled"
                              :placeholder="$t('label_effective_start_date')"
                              :label="$t('label_effective_start_date')+'*'"
                              v-model="params.effective_start_date"></mrc_ess_datePicker>
        </v-col>
        <v-col class="d-flex"
               cols="12"
               sm="6">
          <mrc_ess_datePicker :errMsg="$errors('params','effective_end_date',[{rule: 'afterData',msg: '不能低于有效开始时间'}])"
                              @blur="$v.params.effective_end_date.$touch()"
                              :disabled='disabled'
                              :placeholder="$t('label_effective_end_date')"
                              :label="$t('label_effective_end_date')"
                              v-model="params.effective_end_date"></mrc_ess_datePicker>
        </v-col>
      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn :successText="$store.state.uuid?$t('提交'):$t('label_The_next_step')"
                       @success='success'
                       v-if="!disabled"
                       @cancel='cancel'></mrc_ess_submitBtn>
  </v-card>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import {
  listData,
  getData,
  updateData,
  addData,
  delData,
  updatebasis
} from '@/service/api/data-access-strategy'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
  },
  data () {
    return {
      params: {
        org_level_uuid: '',
        effective_start_date: '',
        effective_end_date: ''
      },
      list: []
    };
  },

  computed: {},
  created () { },
  watch: {},
  async mounted () {
    if (this.$store.state.uuid) {
      await this.getList()
    }
    else {
    }
  },
  methods: {
    async getList () {
      if (this.$store.state.uuid) {
        const res = await getData({ org_level_uuid: this.$store.state.uuid, date: 'mrbase_sys_data_strategy_same_post_grade' })
        if (res.code == 200) {
          // this.list = res.mrbase_sys_data_strategy_same_post_grade ? res.mrbase_sys_data_strategy_same_post_grade : []
          this.params = res.data
        }
        else {
          this.$Message({ text: res.msg, color: rgb(0, 0, 0, 0.38) })
        }
      }
      else {

      }
    },
    async success () {
      if (this.$store.state.uuid) {//修改
        const res = await updatebasis({
          ...this.params,
        })
        if (res.code == 200) {
          this.$Message({ text: this.$t('修改成功') })
          await this.getList()
          this.$emit('updateSuccess')
        }
        else {
          this.$Message({ text: this.$t(res.msg), color: 'rgb(0, 0, 0, 0.38) ' })
        }
      }
      else {//新增
        this.$emit('success', this.params)
      }
    },
    cancel () {
      if (this.$store.state.uuid) {
        this.$myConfirm({ content: this.$t('确认取消编辑') }).then(res => {
          this.$router.push({ name: 'data-access-strategy' })
        }).catch(err => { })
      }
      else {
        this.$emit('cancel')
      }
    },
  },
  validations: {
    params: {
      org_level_uuid: {
        required
      },
      effective_start_date: {
        required,
        beforeData (value) {
          if (!this.params.effective_end_date) return true
          const prevData = new Date(value).valueOf()
          const nextData = new Date(this.params.effective_end_date).valueOf()
          return prevData < nextData
        }
      },
      effective_end_date: {
        // required,
        afterData (value) {
          if (!this.params.effective_start_date) return true
          if (!this.params.effective_end_date) return true
          const nextData = new Date(value).valueOf()
          const prevData = new Date(this.params.effective_start_date).valueOf()
          return prevData < nextData
        }
      },
    }
  },
};
</script>

